/* css reset */
* {
    margin: 0;
    padding: 0;
}

/* main */
#main {
    margin: 5rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

#main .container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 2rem 2rem;
    width: 25rem;
    border: 1px solid #0000004f;
    border-radius: 10px;
}

/* title and logo */
#main .container .title{
    display: flex;
    align-items: center;
    margin-bottom: 14px;
}
#main .container .title h1{
    color: blue;
}
#main .container .title img{
    width: 55px;
}

/* form editing */
#main .container .todo-form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

#main .container .todo-form input {
    width: 100%;
    font-size: 20px;
    padding: 10px 15px;
}

#main .container .todo-form .save {
    font-size: 30px;
    padding: 8px 0px;
}

#main .container .todo-form button {
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 22px;
    margin-left: 0;
    background-color: orangered;
    color: white;
    border: none;
    cursor: pointer;
}

/* todo's container */
#main .container .todo-container {
    width: 100%;
    margin-top: 10px;
}

/* todo's */
#main .container .todo-container .todo {
    display: flex;
    justify-content: space-between;
    padding: 10px 5px;
    cursor: pointer;
    margin: 1rem 0rem;
    transition: 0.3s;
}

#main .container .todo-container .todo:hover {
    background-color: #0000000f;
}

#main .container .todo-container .todo .icon {
    font-size: 30px;
}

#main .container .todo-container .todo .inner-left-todo {
    display: flex;
    justify-content: center;
}

#main .container .todo-container .todo .inner-left-todo .check {
    color: green;
    margin-right: 10px;
    transition: 0.3s;
}

#main .container .todo-container .todo .inner-left-todo p {
    width: 100%;
    font-size: 20px;
}

#main .container .todo-container .todo .inner-left-todo .line {
    text-decoration: line-through;
}

#main .container .todo-container .todo .inner-right-todo {
    display: flex;
    justify-content: center;
}

#main .container .todo-container .todo .inner-right-todo .edit {
    color: #0000007a;
    margin-right: 10px;
    transition: 0.3s;
}

#main .container .todo-container .todo .inner-right-todo .edit:hover {
    color: #458eb6;
}

#main .container .todo-container .todo .inner-right-todo .delete {
    color: #0000007a;
    transition: 0.3s;
}

#main .container .todo-container .todo .inner-right-todo .delete:hover {
    color: red;
}


/* tasks additional information */
#main .container .add-info{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

#main .container .add-info h4{
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* color: #0000007a; */
}

#main .container .add-info h4 span{
    color: red;
    margin-left: 12px;
}

#main .container .add-info button{
    font-size: 20px;
    background-color: #ff0000d4;
    color: white;
    transition: 0.3s;
    cursor: pointer;
    padding: 8px 13px;
    border: none;
    border-radius: 7px;
}

@media screen and (max-width:460px) {
    #main .container {
        padding: 2rem 1rem;
        width: 19rem;
    }
    #main .container .todo-container .todo .inner-left-todo p {
        font-size: 16px;
    }
}